<template>
  <div>
    <div class="stat__header">
      <div class="fl">店铺地域分析</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="14" class="stat__inner">
        <div id="areaChart" class="line-chart"></div>
      </el-col>
      <el-col :span="10" class="border-left">
        <div class="table-box">
          <table class="stat-inner__table mt-20">
            <tr>
              <th style="width:10%">序号</th>
              <th style="width:30%">地区</th>
              <th style="width:30%">店铺数</th>
              <th style="width:30%">占比</th>
            </tr>
          </table>
          <div class="table-box__inner">  
          <table  class="stat-inner__table ">
            <tr v-for="(item,index) in tableData" :key="index">
              <td  style="width:10%">{{index+1}}</td>
              <td  style="width:30%">{{item.areaName}}</td>
              <td  style="width:30%">{{item.storeNum}}</td>
              <td  style="width:30%">{{item.storePrecent}}%</td>
            </tr>
          </table>
           </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import echarts from 'echarts'
const geoJson = require('./../../../../../static/lib/china.json')
import { statisticsStoreArea } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'areaChart',
      optionsData: {
        tooltip: {
          trigger: 'item',
          formatter: '{b}<br/>{c}人'
        },
        visualMap: {
          realtime: false,
          calculable: true,
          left: 20,
          inRange: {
            color: ['lightskyblue', 'yellow', 'orangered']
          }
        },
        series: [
          {
            name: '店铺区域分布',
            type: 'map',
            mapType: 'china', // 自定义扩展图表类型
            itemStyle: {
              normal: { label: { show: true }},
              emphasis: { label: { show: true }}
            },
            data: []
          }
        ]
      },
      tableData: []
    }
  },
  methods: {
    getData() {
      this.loading = true
      statisticsStoreArea(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.series[0].data = res.data.data
            this.tableData = res.data.area
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    initChart() {
      // 初始化图表
      this.$nextTick(() => {
        if (this.el && this.el !== '') {
          echarts.registerMap('china', geoJson)
          this.chart = echarts.init(document.getElementById(this.el), 'walden')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 450px;
}
.table-box {
  height: 470px;
  width: 100%;
  padding: 0 20px;
  &__inner {
    height: 400px;
    overflow: auto;
    width: 100%;
  }
}
</style>



