<template>
  <div>
    <div class="stat__header">
      <div class="fl" style="line-height:28px">店铺交易数据</div>
      <div class="fr">维度:
        <el-select v-model="queryParams.type" @change="query">
          <el-option label="店铺" :value="1"></el-option>
          <el-option label="时间" :value="2"></el-option>
        </el-select>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24">
        <div class="table-box">
          <el-table :data="tableData" style="width: 100%" stripe ref="dataTable">
            <el-table-column
              label="序号"
              show-overflow-tooltip
              class-name="text-overflow"
              width="100"
              align="center"
            >
              <template
                slot-scope="scope"
              >{{(queryParams.page-1)*queryParams.size+(scope.$index+1)}}</template>
            </el-table-column>
            <el-table-column label="日期" v-if="queryParams.type===2">
              <template slot-scope="scope">{{$moment(scope.row.statisticsDate,'YYYY-MM-DD')}}</template>
            </el-table-column>
            <el-table-column label="店铺名称" show-overflow-tooltip class-name="text-overflow"  v-if="queryParams.type===1">
              <template slot-scope="scope">
               {{scope.row.storename}}
              </template>
            </el-table-column>
            <el-table-column label="交易店铺数量" show-overflow-tooltip class-name="text-overflow"   v-if="queryParams.type==2">
              <template slot-scope="scope">
               {{ $cms.float(scope.row.storeNum,0)}}
              </template>
            </el-table-column>
            <el-table-column label="订单数" show-overflow-tooltip class-name="text-overflow">
              <template slot-scope="scope">
                {{queryParams.type==1? $cms.float(scope.row.storeOrderNum,0)
                :$cms.float(scope.row.dayOrderNum,0)}}
              </template>
            </el-table-column>
            <el-table-column label="销售金额">
              <template slot-scope="scope">
                ¥{{queryParams.type==1?
                $cms.float(scope.row.storeSellMoney)
                :$cms.float(scope.row.daySellMoney)}}
              </template>
            </el-table-column>
          </el-table>
          <cms-pagination @change="getPage" :total="total"></cms-pagination>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
const moment = require('moment')
import chartMixins from '../mixins.js'
import list from '@/mixins/list'
import { statisticsStorePage } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins, list],
  data() {
    return {
      newStatistics: {
        sumMemberCount: 0,
        sumNewMember: 0,
        sumRechargeMember: 0
      },
      queryParams: {
        page: 1,
        size: 10,
        type: 1,
        startTime: moment()
          .subtract(7, 'days')
          .format('YYYY-MM-DD'),
        endTime: moment()
          .subtract(1, 'days')
          .format('YYYY-MM-DD')
      }
    }
  },
  methods: {
    query(val) {
      this.getData()
    },
    getData() {
      this.getTableData(statisticsStorePage, this.queryParams)
    }
  }
}
</script>
<style lang="scss" scoped>
.table-box {
  width: 100%;
}
.stat__body {
  padding: 20px;
}
</style>



