<template>
  <div>
    <div class="stat__header">
      <div class="fl">行业店铺分布 </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <div id="storeCategoryChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { statisticsStoreCategory } from './../../../../api/statistics/index'
export default {
  mixins: [chartMixins],
  data() {
    return {
      value: '订单趋势',
      el: 'storeCategoryChart',
      optionsData: {
        grid: {
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          containLabel: true
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        xAxis: {

          type: 'value',
          boundaryGap: [0, 0.1]
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: []
        },
        series:
          {
            type: 'bar',
            barMaxWidth: 25,
            barCategoryGap: '10%',
            data: [9000, 6000, 2000, 1000, 200, 100]
          }

      }
    }
  },
  methods: {

    getData() {
      this.loading = true
      statisticsStoreCategory(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.yAxis.data = res.data.yAxis
            this.optionsData.series.data = res.data.xAxis
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



