<template>
  <div>
    <div class="stat__header">
      <div class="fl">店铺概况
        <el-tooltip placement="right" effect="light">
          <div slot="content">人数都按照相同用户多次记一次的规则进行统计
            <br>售后申请的数量及金额不影响下单、付款的笔数及金额
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner" style="padding-bottom:0px">
        <el-row class="stat-card__list" :gutter="20">
          <el-col :span="6">
            <div class="stat-card">
              <p>入驻店铺总数</p>
              <count-to :endVal="newStatistics.storeNum" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>新增入驻数</p>
              <count-to :endVal="newStatistics.addstoreNum" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>第三方店铺数</p>
              <count-to :endVal="newStatistics.thirdStoreNum" class="count-val"></count-to>
              <span class="up card-prop">(占比{{newStatistics.thirdStorePercent}}%)</span>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>自营店铺数</p>
              <count-to :endVal="newStatistics.selfStoreNum" class="count-val"></count-to>
              <span class="up card-prop">(占比{{newStatistics.selfStorePercent}}%)</span>
            </div>
          </el-col>
        </el-row>
        <div style="overflow:hidden">
          <el-radio-group v-model="value" @change="changeData" size="mini" style="float:right">
            <el-radio-button label="订单趋势"></el-radio-button>
            <el-radio-button label="销量趋势"></el-radio-button>
          </el-radio-group>
        </div>
        <div id="storeLineChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { statisticsStoreSummarize } from './../../../../api/statistics/index'
export default {
  mixins: [chartMixins],
  data() {
    return {
      value: '订单趋势',
      el: 'storeLineChart',
      newStatistics: {
        addstoreNum: 0,
        selfStoreNum: 0,
        selfStorePercent: 0,
        storeNum: 0,
        thirdStoreNum: 0,
        thirdStorePercent: 0
      },
      optionsData: {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['累积总数', '新增数', '活跃数', '成交会员数'],
          left: 30,
          top: 20
        },
        grid: {
          top: 30,
          left: 0,
          right: 30,
          bottom: 30,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dashed'
            }
          },
          data: ['00-00', '0-0', '0-0']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value'
          }
        ],
        series: {
          name: '订单数',
          type: 'line',
          smooth: true,
          areaStyle: {},
          data: []
        }
      }
    }
  },
  methods: {
    changeData(val) {
      if (val === '订单趋势') {
        this.optionsData.series.name = '订单数'
        this.optionsData.series.data = this.newStatistics.yAxisOrder
      } else {
        this.optionsData.series.name = '销售额'
        this.optionsData.series.data = this.newStatistics.yAxisSell
      }
      this.chart.setOption(this.optionsData)
    },
    getData() {
      this.loading = true
      statisticsStoreSummarize(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.newStatistics = Object.assign({}, res.data)
            this.optionsData.xAxis.data = res.data.xAxis
            this.optionsData.series.data = res.data.yAxisOrder
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



