<template>
<div class="stat__layout">
 <timer @change="getTime"></timer>
 <general class="mt-20" :time-data="value"></general>
  <trading class="mt-20" :time-data="value"></trading>
  <category class="mt-20" :time-data="value"></category>
  <store-area  class="mt-20" :time-data="value"></store-area>
</div>
</template>

<script>
 
import timer from './../components/time'
import general from './general'
import trading from './trading'
import category from './category'
import storeArea from './area.vue'
export default {
   components: {
     general,
     trading,
     category,
     storeArea,
     timer
   },
   data() {
     return {
       value: ['', '']
     }
   },
   methods: {
     getTime(time) {
       this.value = time
     }
   }
}
</script>

<style>
</style>



